.courses {
  text-align: center;
  .section-title {
    font-size: 1.5rem;
    color: $mainColor;
    @include padding;
    @include letter-spacing; }
  h3 {
    font-size: 1.25rem;
    color: $mainColor;
    opacity: 0.9;
    margin: 0;
    @include padding;
    @include letter-spacing; }
  .course-name {
    color: $mainColor;
    opacity: 0.7;
    @include letter-spacing;
    font-size: 1rem;
    cursor: pointer;
    line-height: 1.2rem;
    @include transition;
    @include padding;
    margin: 0;
    &:hover {
      color: #fff;
      background: $mainColor;
      opacity: 1; } } }

.info {
  background: $mainColor;
  text-align: left; }

.info-corso {
  color: #fff;
  opacity: 0.9;
  @include padding;
  @include letter-spacing;
  font-size: 0.8rem;
  line-height: 1.2rem; }

.descrizione-corso,
.programma-corso {
  color: #fff;
  padding-top: 15px;
  padding-left: 15px;
  margin: 0;
  @include letter-spacing;
  font-size: 0.9rem; }

.more-info {
  color: #fff;
  text-decoration: none;
  &:hover {
    color: $secondaryColor; } }

.course-img {
  width: 100%; }

.divider {
  @include divider; }
