#prezzi {
  color: $mainColor;
  @include padding;
  text-align: center;

  h2 {
    font-size: 1.3rem; }

  .trattamento {
    font-size: 1.1rem;
    margin: 0;
    padding: $gutterWidth 0;
    @include letter-spacing; }

  .costo-trattamento {
    width: 100%;
    background: $mainColor;
    color: #fff;
    padding: 10px 0;
    @include letter-spacing; }

  .desc-trattamento {
    @include letter-spacing;
    @include padding; }

  #corsi-distanza {
    margin: 0;
    padding: $gutterWidth 0; }

  .info-corsi-dist {
    @include letter-spacing;
    @include padding;

    a {
      color: $mainColor;
      font-weight: 700;
      @include transition;
      &:hover {
        color: $secondaryColor; } } } }
