@media screen and (min-width: 730px) {
  .main-card,
  .general-info {
    width: 600px;
    margin: 30px auto !important; }

  .navbar {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0; }

  .logo {
    width: 500px;
    margin: 0; }

  .hamburger-menu {
    display: none; }

  .nav-content {
    flex-direction: row;
    justify-content: center;
    background: $mainColor;
    position: relative;
    @include flex;

    .nav-link {
      margin: 15px;
      padding: 15px;
      font-size: 1.25rem;
      border: none;
      border-radius: 5px;
      @include transition;
      &:hover {
        background: $secondaryColor; } }

    .content {
      position: absolute;
      width: 100%;
      left: 0;
      @include transition;

      .course-link {
        color: #fff;
        text-decoration: none;
        padding: 20px 0;
        display: flex;
        justify-content: center;
        @include transition;
        &:first-child {
          border-top: 1px solid $mainColor;
          &:hover {
            border-top: 1px solid $secondaryColor; } } } } } }
