@mixin flex {
  display: flex; }

@mixin divider {
  width: 80%;
  background: $mainColor;
  height: 3px;
  margin: 10px auto;
  border-radius: 5px; }


@mixin transition {
  transition: all 400ms ease-in-out; }

@mixin link {
  text-decoration: none;
  color: #fff;
  letter-spacing: 1.2px; }

@mixin padding {
  padding: 15px; }

@mixin letter-spacing {
  letter-spacing: 1.2px; }
